.about {
    background-color: var(--second-bg-color);

    min-height: 75vh;
    padding: 5rem 15% 5rem 15%;

    display: flex;
    align-items: center;
    justify-content: center;

    overflow: scroll;

    box-sizing: border-box;

}

.about-container{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.about-images{
    position: relative;
    top: 0;
    left: 0;
}

.computer-image{
    position: relative;
    top: 0;
    left: 0;
    width: 30vw;
    border-radius: 5rem;
    z-index: 0;
    box-shadow: 0 0 .75vw var(--main-color);
}

.emoji-container{
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    bottom: -3vw;
    right: -3vw;

    width: 12vw;
    aspect-ratio: 1;

    border-radius: 50%;
    background-color:var(--second-bg-color);
}

.ham-emoji-image{
    width: 7vw;
    animation: ham-rotate 4s infinite linear;   
}

@keyframes ham-rotate {
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

.about-content{ 
    width: 20vw;
    padding-left: 5vw;
    text-align: left;
    /* margin-left: 5vw; */
}

#about-heading-me{
    color: var(--main-color);
}

.about-content h2{
    line-height: 1.2;
    font-size: 4.5rem;
}

.about-content h3{
    font-size: 2.6rem;
    width: 35vw;
}

.about-content p{
    font-size: 1.6rem;
    margin: 2rem 0 0;
    width: 20vw;
}