.portfolio {
    background-color: var(--bg-color);

    min-height: 100vh;
    padding: 5rem 15% 5rem;

    overflow: scroll;

    box-sizing: border-box;
}

.portfolio-container-heading{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 2vw 0 4vw 0;
}

.portfolio-heading{
    font-size: 6rem;
}

.portfolio-heading span{
    color: var(--main-color);
}

.portfolio-container-projects{
    display: grid;
    grid-template-columns: repeat(1fr, 1);
    align-items: center;
    justify-content: center;
    gap: 2vw;

}

.portfolio-box{
    position: relative;
    border-radius: 2vw;

    width: 60vw;
    height: 50vh;

    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 1vw 0% 1vw 0;

    background-color: var(--second-bg-color);

    box-shadow: 0 0 .5vw 0 rgb(200, 200, 200);
}

.portfolio-box-image-container{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30vw;
    height: 42.5vh;
    border-radius: 2vw;

    /* background-color: cyan; */

    overflow: hidden;

    box-shadow: 0 0 1vw rgb(200, 200, 200);
    transition: .5s ease;
}

.portfolio-box-image-container:hover{
    box-shadow: 0 0 1vw var(--main-color);
}

.stock-img{
    max-width: 100%;
    transition: 1.5s ease-in;
}

.stock-img:hover{
    transform: scale(1.2);
    transform-origin: center;
}

.portfolio-content{
    height: inherit;
}

.portfolio-content-text {
    display: block;
    align-items: center;
    justify-content: center;

    text-align: center;
    
}

.portfolio-content h4{
    padding: 5vw 0vw 0vw 0vw;
    font-size: 3rem;
}

.portfolio-content p{
    width: 20vw;
    margin-top: 1vw;
    font-size: 1.5rem;

    color: var(--text-color);
    font-weight: 200;
    text-align: center;
}

.portfolio-content-links{
    display: flex;
    align-items: center;
    justify-content: center;

    padding-top: 5vh;

    font-size: 2rem;

}

.portfolio-content-links span{
    font-size: 1.5rem;
    padding: .5vw;
}

.github-container{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 3rem;

    color: var(--main-color);
    transition: 0.2s;
}

.github-container:hover{
    color: var(--text-color);
}

.text-github{
    font-size: 2rem;
    margin-right: .5vw;
    font-weight: 600;
}

.icon-github{
    display: flex;
    align-items: center;
    font-size: 3.5rem;
}

