.header{
    background-color: var(--bg-color);

    box-sizing: border-box;

    position: fixed;
    top: 0;
    left: 0;

    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 2rem 9%;
    width: 100%;
    z-index: 100;
    height: 7.5vh;
    min-height: 7.5vh;

    box-shadow: 0 0 1rem rgb(200, 200, 200);
}

.homepage-logo{
    font-size: 2.5rem;
    font-weight: 600;
    text-decoration: none;
    color: var(--main-color);
    cursor: default;
}

.homepage-logo:hover{
    color: var(--text-color);
}

.navbar a{
    font-size: 1.7rem;
    color: var(--text-color);
    text-decoration: none;
    font-weight: 500;
    margin-left: 4rem;
    transition: .3s ease;
}

.navbar a:hover,
.navbar a.active{
    color: var(--main-color);
}

#menu-icon{
    font-size: 3.6rem;
    color: var(--text-color);
    display: none;
}