.contact{
    background-color: var(--second-bg-color);


    padding: 5rem 15% 5rem;

    height: 30vh;
}

.contact-text{

}

.contact-text h2{
    line-height: 1.2;
    font-size: 5rem;
}

.contact-text h4{
    font-size: 2.5rem;
    color: var(--main-color);
}

.contact-box-container{
    display: flex;
    align-items: center;
}

.contact-item-container{
    display: flex;
    align-items: center;
    /* justify-content: center; */

    margin-top: 3vw;
    margin-right: 5vw;
    color: var(--text-color);

    height: 4vw;
}

.contact-container-icon{
    width: 4vw;
    height: 4vw;
    border-radius: 50%;


    background-color: var(--second-bg-color);
    box-shadow: 0 0 0.5vw 0 rgb(200,200,200);
}

.contact-icon{
    font-size: 3vw;
    text-align: center;
    color: var(--main-color);
}

.contact-item-text{
    font-size: 2rem;
    padding-left: 2rem;
    color: var(--second-text-color);
}

.contact-item-text h5{
    font-size: 2.5rem;
    font-weight: bold;
    color: var(--main-color);
}

.contact-item-text span{
    font-size: 2rem;
    font-weight: 400;
}

.contact-item-text a{
    font-size: 2rem;
    font-weight: 400;
    transition: .2s;
    color: var(--second-text-color);
}

.contact-item-text a:hover{
    color: var(--main-color)
}