.home {
    background-color: var(--bg-color);
    min-height: 100vh;
    padding: 10rem 9% 2rem;

    display: flex;
    align-items: center;
    justify-content: space-between;

    overflow: scroll;

    box-sizing: border-box;
}

.home-content{
    width: 50vw;
    /* border: 2px solid blue; */
}

.home-content h1{
    font-size: 5.6rem;
    font-weight: 700;
    line-height: 1.3;
}

#ham-emoji{
    padding-left: 2rem;
}

.home-content h3{
    font-size: 3.2rem;
    font-weight: 700;
    color: var(--main-color);
    margin-bottom: 3rem;
}

.home-content p{
    font-size: 1.6rem;
    margin: 1rem 0rem 1rem;
    width: 35vw;
}

.home-img img{
    width: 25vw;
    border-radius: 100%;
    /* border: 5px solid purple; */
    box-shadow: 0 .25vw .75vw 0 var(--main-color);
    margin-right: 5vw;
    animation: floating 3s ease-in-out infinite;
}

@keyframes floating {
    0% { transform: translate(0,  -15px); }
    50%  { transform: translate(0, 0px); }
    100%   { transform: translate(0, -15px); }   
}

.home-tech-stack{
    display: flex;
    align-items: center;
    font-size: 2rem;
    padding-bottom: 2vw;
}

.home-tech-stack h4{
    display: flex;
    align-items: center;
    font-size: 2rem;
    font-weight: 400;
    color: var(--second-text-color);
}

#home-tech-stack-brain{
    padding: 0 0 0 1rem;
    font-size: 3rem;
}

#home-tech-stack-divider{
    font-size: 5rem;
    font-weight: 100;
    padding: 0 5rem 0 1rem;
    color: var(--main-color);
    cursor: default;
}

.home-tech-stack-icons{
    padding: 0 0 0 3rem;
}

.home-tech-stack-icons a{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 5rem;
    height: 5rem;
    background-color: transparent;
    font-size: 4rem;
    color: var(--text-color);
    margin: 3rem 2rem 3rem 0;
    transition: .25s;
    cursor: default;
}

.social-media a{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 5rem;
    height: 5rem;
    background-color: transparent;
    border: .2rem solid var(--main-color);
    border-radius: 50%;
    font-size: 2.5rem;
    color: var(--main-color);
    margin: 3rem 1.5rem 3rem 0;
    transition: .25s;
}

.social-media a:hover{
    color: var(--bg-color);
    background-color: var(--main-color);
    box-shadow: 0 0 1rem var(--main-color);
}

/* Currently removed due to being a bit much */
.resume-button{
    display: inline-block;
    padding: 1rem 2.8rem;
    background-color: var(--main-color);
    border-radius: 4rem;
    box-shadow: 0 0 0.5rem var(--main-color);
    font-style: 1.6rem;
    color: var(--bg-color);
    letter-spacing: .1rem;
    font-weight: 600;
    font-size: 2rem;
}

.resume-button:hover{
    box-shadow: none;
}
